import Login from '../router/login/LoginAsync';
import Jsl from '../router/jsl/jslAsync';
export default [
    {
        path:'/login',
        component:Login,
        exact:false
    },{
        path:'/jsl',
        component:Jsl,
        exact:false
    },]


