
import React, { Suspense, lazy } from 'react';
import { createBrowserHistory} from 'history';
import { Switch, Router, Redirect} from 'react-router-dom';
import {ConnectedRouter} from 'connected-react-router';
import {History} from 'history';
import ErrorBoundary from './components/ErrorBoundary';
import ErrorBoundaryRoute from './components/ErrorBoundaryRoute';
import routerconfig from './RouterConfig';

import './App.scss';
import './style/style.scss'

const Home = lazy(()=> import('./router/home/Home'));


export interface Iprops {
  history: History,
}

interface PrivateRouteProps{
  path: string,
  component:any,
  isLogin: Boolean,
  history: History,
}
interface Istate {
}
class PrivateRoute extends React.Component <PrivateRouteProps> {

  public render() {
    const { path, history } = this.props
    const isLogin = JSON.parse(localStorage.getItem('isLogin')) || false
      console.log(history);
      if (isLogin) {
          return (
              <ErrorBoundaryRoute path={path} component={Home} />
          )
      }else{
          return (
              <Redirect from={path} to='/login'/>
          )
      }
  }
}
interface NoMatchProps{
  location: {}
}
class NoMatch extends React.Component<NoMatchProps>{
  public render(): JSX.Element {
      return (
          <h1>NoMatch for {location.pathname}</h1>
      )
  }
}
class App extends React.Component< Iprops, Istate > {
  public render() {
    const { history } = this.props
    return (
      <ConnectedRouter history={ history }>
      
        <div className="App">
          <Router history={ createBrowserHistory() }>
            <div style={{display:'flex'}}>
                    <div style={{flex: 1}}>
                    <Suspense fallback={<div>Loading...</div>}>
                    <ErrorBoundary>
                      <Switch>
                      {routerconfig.map((item, index) => (
                            <ErrorBoundaryRoute
                              key={index}
                              path={item.path}
                              component={item.component}
                              exact={item.exact}
                            />
                          ))}
                        <PrivateRoute isLogin={false} history={history}  path='/' component={Home}/>
                        <ErrorBoundaryRoute component={NoMatch}/>
                      </Switch>
                    </ErrorBoundary>
                    </Suspense>
                    </div>
                </div>
          </Router>
        </div>
        </ConnectedRouter>
    );
  }
}

export default App;



